import React from 'react'
import { Link } from 'gatsby'
import styled from 'styled-components'

const TitleContainer = styled.span`
  font-weight: 500;
  font-size: 24px;
  color: #2e405b;

  &:hover {
    border-bottom: solid #2e405b 2px;
  }
`

const Title = ({ to, children }) => (
  <Link
    to={to}
    style={{
      textDecoration: 'none',
    }}
  >
    <TitleContainer>
      {children}
    </TitleContainer>
  </Link>
)

export default Title
