import { Link } from 'gatsby'
import PropTypes from 'prop-types'
import React from 'react'
import styled from 'styled-components'

import Navigation from '../components/navigation'

const Title = styled.span`
  margin-right: 12px;
  font-weight: 500;

  &:hover {
    border-bottom: 2px solid rgb(46, 64, 91);
  }
`;

const Header = ({ siteTitle }) => (
  <div>
    <div
      style={{
        margin: `0 auto`,
        maxWidth: 700,
        paddingTop: '1.45rem',
        paddingBottom: '0rem',
        paddingLeft: '1rem',
        paddingRight: '1rem',
      }}
    >
      <h1 style={{ margin: 0 }}>
        <Link
          to="/"
          style={{
            textDecoration: `none`,
          }}
        >
          {siteTitle}
        </Link>
      </h1>
      <Navigation>
        <Link
          to="/"
          style={{
            textDecoration: 'none',
          }}
        >
          <Title>首页</Title>
        </Link>
        <Link
          to="/aboutme"
          style={{
            textDecoration: 'none',
          }}
        >
          <Title>关于我</Title>
        </Link>
      </Navigation>
    </div>
  </div>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
